import React from 'react';

const DeliveryModalComponent = ({modal, deliveryModalCloseFn}) => {

  const onClickClose=()=>{
    deliveryModalCloseFn();
  }

  return (
    modal.isShowDelivery && (
      <div id='deliveryModal'>
        <div className="layer-container">

          {
            modal.isShowLayer1 && (
              <div className="layer layer-star">
                <ul>
                  <li><strong className="address-idx">{modal.addressTit}</strong> <br /> .</li>
                  <li className="img-gap"><img src="./img/img_delivery_kurly.png" alt="" /></li>
                  <li className="layer-txt"> ,    .</li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn"></button>
                </div>
              </div>
            )
          }

          {
            modal.isShowLayer2 && (
              <div className="layer layer-nomal">
                <ul>
                  <li> .</li>
                  <li className="img-gap"><img src="./img/img_delivery_kurly_nomal.png" alt="" /></li>
                  <li className="layer-txt">     </li>
                  <li className="layer-txt2">     </li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn"></button>
                </div>
              </div>
            )
          }

          {
            modal.isShowLayer3 && (
              <div className="layer layer-none">
                <ul>
                  <li>  .</li>
                  <li className="img-gap"><img src="./img/img_delivery_none.png" alt="" /></li>
                  <li className="layer-txt">       .</li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn"></button>
                </div>
              </div>
            )
          }

        </div>
      </div>
    )
    
  );
};

export default DeliveryModalComponent;